 * and in the editor.
 *
 * Replace them with your own styles or remove the file completely.
 */

.draad-adreszoeker {
	*,
	*::before,
	*::after {
		box-sizing: border-box;
	}

	form {
		display: grid;
		grid-template-columns: 1fr;
		gap: 8px;
		width: 100%;
		margin: 0;
		position: relative;
	}

	&__filters {
		margin-bottom: 1em;
		display: grid;
		grid-template-columns: 1fr 1fr;
		gap: 8px;
		width: 100%;
		margin-top: 50px;

		@media (max-width: 600px) {
			grid-template-columns: 1fr;
		}

		input[list],
		input[type='number'] {
			border: 1px solid #757575;
			background-color: #ffffff;
			padding: 13px 20px;
			color: #4b4b4b;
			font-size: 15px;
			line-height: 1.73;
			width: 100%;

			&::-webkit-input-placeholder {
				opacity: 0.7;
			}

			&::-moz-placeholder {
				opacity: 0.7;
			}

			&:-moz-placeholder {
				opacity: 0.7;
			}
		}

		input[type='submit'] {
			padding: 10px 20px;
			font-size: 18px;
			line-height: 1.66;
			color: #fff;
			display: inline-flex;
			margin-top: 12px;
			border-radius: 2px;
			border: none;
			cursor: pointer;
			width: fit-content;
			background: #1d6b34;
			transition: all 300ms ease;

			&:hover {
				color: #000;
				background: #f8d62d;

				.arrow-down {
					&:before {
						color: #000;
					}
				}
			}
		}

		input[type='number'] {
			@media (max-width: 600px) {
				margin-top: 30px;
			}
		}

		input[type='number']:disabled {
			background-color: #d2d2d2;
		}

		.fcomplete-wrap {
			box-shadow: 0 5px 24px 0 rgb(0 0 0 / 21%);
			z-index: 1;
			border: 0;
			height: 200px;
			overflow-y: auto;

			.fcomplete-result {
				padding: 5px 16px;
				font-size: 15px;
				line-height: 1.66;
				color: #000;
				transition: all 300ms ease;

				&:hover {
					color: #fff;
					background: #1d6b34;
				}
			}
		}
	}
}
